.partner-url {
    background-color: #ffffff;
    width: 500px;
    padding: 20px;
    height: 100px;
    border-radius: 15px;
    padding-top: 10px;
    padding-bottom: 15px;
    margin-bottom: 15px;
    box-shadow: 2px 10px 16px 0px rgba(0,0,0,0.14);
    p {
        margin-left: 15px;
        font-weight: bold; } }

.partner-url__input {
    display: block;
    height: 50px;
    width: 96%;
    padding-left: 15px;
    font-size: 20px;
    border-width: 2px;
    border-color: #CCCCCC;
    background-color: #EBEBEB;
    color: #000000;
    border-style: none;
    border-radius: 10px;
    margin-bottom: 15px;
    -webkit-appearance: none /* Убираем рамку в Chrome и Safari */; }

.partner-url__input-div {
    position: relative; }

.partner-url__copy-button {
    position: absolute;
    cursor: pointer;
    top: 6px;
    right: 10px;
    height: 40px;
    border-radius: 10px;
    border: none;
    background-color: #73bc62;
    color: #ffffff;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    &:hover {
        background-color: #5cbf2a; }
    &:active {
        top: 9px;
        right: 8; } }
