.last-completed-tasks {
    width: 540px;
    background-color: #ffffff;
    border-radius: 15px;
    padding-top: 15px;
    padding-bottom: 15px;
    box-shadow: 2px 10px 16px 0px rgba(0,0,0,0.14); }

.last-completed-tasks__title {
    font-weight: bold; }

.last-completed-tasks__menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 15px;
    margin-right: 15px; }

.last-completed-tasks__table {
    text-align: center;
    width: 100%;
    border: none;
    border-spacing: 0;
    tr {}
    td {
        border: none;
        padding: 10px;
        vertical-align: middle; } }

.last-completed-tasks__tr-td__gray {
    background-color: #f5f5f5; }

.last-completed-tasks__download-button {
    border-radius: 10px;
    border: 0;
    background-color: #f5f5f5;
    height: 30px;
    color: #6f6f6f; }

.last-completed-tasks__circle {
    width: 50px;
    height: 50px;
    border-radius: 25px;
    background-color: #d8d8d8; }

.last-completed-tasks__table-row {
    display: flex;
    flex-direction: row;
    width: 100%;
    margin-bottom: 20px;
    align-items: center;
    p {
        margin: 0; }
    &__active {
        @extend .last-completed-tasks__table-row;
        background-color: #f5f5f5; } }

.last-completed-tasks__logo {
    width: 10%;
    margin: 0 20px 0 20px; }

.last-completed-tasks__row-title {
    width: 70%; }

.last-completed-tasks__row-time {
    width: 20%;
    margin-right: 20px;
    text-align: right; }

.last-completed-tasks__name {
    font-weight: bold; }
